import http from "@/lib/request";
import { Result } from "./index.d";
import { LoginParam } from "./userCenter.d";

const login = (params: LoginParam): Promise<Result<any>> => {
  return http.post("api/v1/login", params);
};
const getUserInfo = () => {
  return http.get("api/v1/getUserInfo");
};

export default {
  login,
  getUserInfo
};
