import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Meta from "./meta.d";
import excep from "./routes/excep";

Vue.use(VueRouter);

const initRoutes: RouteConfig[] = [];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes: initRoutes
});

router.beforeEach((to, from, next) => {
  // 动态修改页面标题
  document.title = to?.meta?.title
    ? to.meta.title
    : process.env.DOCUMENT_DEFAULT_TITLE;
  next();
});

export default router;

// 鉴权
const checkAuth = (meta: Meta, role: string): boolean => {
  if (!meta?.role?.length) return true;
  return meta.role.includes(role);
};

const convertRouter = (routes: RouteConfig[], role: string) => {
  const newRutes: RouteConfig[] = [];
  for (const r of routes) {
    const { children, meta } = r;
    if (!checkAuth(meta, role)) continue;
    if (children?.length) r.children = convertRouter(children, role);
    newRutes.push(r);
  }
  newRutes.push(excep);
  return newRutes;
};

export const generateRouter = (routes: RouteConfig[], role: string) => {
  return convertRouter(routes, role);
};
