import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";

interface WaterMarkState {
  mainHeaderActiveName: string;
}

@Module({ name: "watermark", namespaced: true })
export default class Login extends VuexModule {
  mainHeaderActiveName = "0";

  get _mainHeaderActiveName() {
    return this.mainHeaderActiveName;
  }

  @Mutation
  UPDATE_INFO({ mainHeaderActiveName }: WaterMarkState) {
    this.mainHeaderActiveName = mainHeaderActiveName;
  }
}
