





import Vue from "vue";
import routes from "@/router/routes";
import { generateRouter } from "@/router/router";
import { getModule } from "vuex-module-decorators";
import UserStore from "@/store/modules/userCenter";
import "./app.scss";
export default Vue.extend({
  created() {
    this.createRouters();
  },
  computed: {
    $UserStore() {
      return getModule(UserStore, this.$store);
    },
    userRole(): string {
      return this.$UserStore._userRoles[0];
    }
  },
  methods: {
    createRouters() {
      this.$router.addRoutes(generateRouter(routes, this.userRole));
    }
  },
  watch: {
    userRole() {
      this.createRouters();
    }
  }
});
