import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";
interface CacheItem {
  name: string;
  path: string;
}

@Module({ name: "tabView", namespaced: true })
export default class Store extends VuexModule {
  tabCacheList: CacheItem[] = [];
  get cacheList() {
    return this.tabCacheList;
  }
  get tabViewList() {
    return this.tabCacheList.map(item => ({
      path: item.path,
      key: item.path,
      label: item.name
    }));
  }
  get routerCached() {
    return this.tabCacheList.map(item => item.name);
  }
  @Mutation
  ADD_TAB_CACHE(param: CacheItem) {
    if (this.tabCacheList.every(item => item.path !== param.path)) {
      this.tabCacheList.push(param);
    }
  }
  @Mutation
  DELETE_TAB_CACHE(path: string) {
    const index = this.tabCacheList.findIndex(item => item.path === path);
    index !== -1 && this.tabCacheList.splice(index, 1);
  }
  @Mutation
  CLEAR_TAB_CACHE() {
    this.tabCacheList = [];
  }
}
