import { Module, VuexModule, Action, Mutation } from "vuex-module-decorators";
import service from "@/service/userCenter";
import { LoginParam } from "@/service/userCenter.d";

interface Info {
  username: string;
  userRoles: string[];
  token: string;
}

@Module({ name: "userCenter", namespaced: true })
export default class UserCenter extends VuexModule {
  username = "";
  userRoles: string[] = ["watermark"];
  token = "";

  get _token() {
    return this.token;
  }
  get _isLogin() {
    return !!this._token;
  }
  get _userRoles() {
    return this.userRoles;
  }

  @Action
  async login(params: LoginParam) {
    const res = await service.login(params);
    
  }
  @Action
  async getUserInfo() {
    return await service.getUserInfo();
  }

  @Mutation
  UPDATE_INFO({ username, userRoles, token }: Info) {
    this.username = username;
    this.userRoles = userRoles;
    this.token = token;
  }
}
